
"use client";

import React from 'react';
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Calendar } from "@/components/ui/calendar";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { CalendarIcon } from "lucide-react";
import { format } from "date-fns";
import { cn } from "@/lib/utils";
import { useAppContext, type Expense } from '@/contexts/AppContext';
import { useToast } from "@/hooks/use-toast";

interface AddExpenseModalProps {
  isOpen: boolean;
  onClose: () => void;
}

const expenseSchema = z.object({
  name: z.string().min(1, { message: "Expense name is required." }),
  amount: z.coerce.number().positive({ message: "Amount must be positive." }),
  date: z.date({ required_error: "Please select a date." }),
  type: z.enum(['essential', 'variable'], { required_error: "Please select an expense type." }),
  category: z.string().min(1, { message: "Category is required." }),
});

type ExpenseFormValues = z.infer<typeof expenseSchema>;

export function AddExpenseModal({ isOpen, onClose }: AddExpenseModalProps) {
  const { addExpenseToList, fixedExpenseCategories, variableExpenseCategories } = useAppContext();
  const { toast } = useToast();
  
  const form = useForm<ExpenseFormValues>({
    resolver: zodResolver(expenseSchema),
    defaultValues: {
      name: "",
      amount: undefined,
      date: new Date(),
      type: undefined,
      category: "",
    },
  });

  const expenseType = form.watch("type");
  const availableCategories = expenseType === 'essential' ? fixedExpenseCategories : variableExpenseCategories;

  const onSubmit = (data: ExpenseFormValues) => {
    addExpenseToList(data as Omit<Expense, 'id'>);
    toast({
      title: "Expense Added",
      description: `${data.name} for $${data.amount.toFixed(2)} has been recorded.`,
    });
    form.reset(); // Reset form after submission
    onClose();
  };

  return (
    <Dialog open={isOpen} onOpenChange={(open) => {
      if (!open) {
        form.reset(); // Reset form when dialog is closed
        onClose();
      }
    }}>
      <DialogContent className="sm:max-w-md bg-card shadow-xl rounded-lg">
        <DialogHeader>
          <DialogTitle className="text-primary">Add New Expense</DialogTitle>
          <DialogDescription>
            Track your spending by adding an expense with its details.
          </DialogDescription>
        </DialogHeader>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4 py-3 max-h-[70vh] overflow-y-auto pr-2">
          <div>
            <Label htmlFor="name">Expense Name</Label>
            <Input id="name" {...form.register("name")} placeholder="e.g., Coffee" />
            {form.formState.errors.name && <p className="text-sm text-destructive">{form.formState.errors.name.message}</p>}
          </div>
          <div>
            <Label htmlFor="amount">Amount</Label>
            <Input id="amount" type="number" step="0.01" {...form.register("amount")} placeholder="e.g., 5.50" />
            {form.formState.errors.amount && <p className="text-sm text-destructive">{form.formState.errors.amount.message}</p>}
          </div>
          <div>
            <Label htmlFor="date">Date</Label>
            <Popover>
              <PopoverTrigger asChild>
                <Button
                  variant={"outline"}
                  className={cn("w-full justify-start text-left font-normal", !form.watch("date") && "text-muted-foreground")}
                >
                  <CalendarIcon className="mr-2 h-4 w-4" />
                  {form.watch("date") ? format(form.watch("date")!, "PPP") : <span>Pick a date</span>}
                </Button>
              </PopoverTrigger>
              <PopoverContent className="w-auto p-0 bg-card">
                <Calendar mode="single" selected={form.watch("date")} onSelect={(date) => form.setValue("date", date || new Date())} initialFocus />
              </PopoverContent>
            </Popover>
            {form.formState.errors.date && <p className="text-sm text-destructive">{form.formState.errors.date.message}</p>}
          </div>
          <div>
            <Label htmlFor="type">Expense Type</Label>
            <Select onValueChange={(value) => form.setValue("type", value as 'essential' | 'variable')} value={form.watch("type")}>
              <SelectTrigger>
                <SelectValue placeholder="Select type" />
              </SelectTrigger>
              <SelectContent className="bg-card">
                <SelectItem value="essential">Essential Fixed</SelectItem>
                <SelectItem value="variable">Variable</SelectItem>
              </SelectContent>
            </Select>
            {form.formState.errors.type && <p className="text-sm text-destructive">{form.formState.errors.type.message}</p>}
          </div>
          {expenseType && (
            <div>
              <Label htmlFor="category">Category</Label>
              <Select onValueChange={(value) => form.setValue("category", value)} value={form.watch("category")}>
                <SelectTrigger>
                  <SelectValue placeholder="Select category" />
                </SelectTrigger>
                <SelectContent className="bg-card">
                  {availableCategories.map(cat => <SelectItem key={cat} value={cat}>{cat}</SelectItem>)}
                </SelectContent>
              </Select>
              {form.formState.errors.category && <p className="text-sm text-destructive">{form.formState.errors.category.message}</p>}
            </div>
          )}
          <DialogFooter className="pt-4">
            <Button type="button" variant="outline" onClick={() => { form.reset(); onClose(); }}>Cancel</Button>
            <Button type="submit" className="bg-accent hover:bg-accent/90 text-accent-foreground">Add Expense</Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}

