
"use client";

import type { Expense } from '@/contexts/AppContext';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Badge } from "@/components/ui/badge";
import { format } from "date-fns";
import { Utensils, Home, ShoppingCart, Car, Briefcase, Activity } from 'lucide-react'; // Example icons

interface ExpenseListProps {
  title: string;
  expenses: Expense[];
  type: 'essential' | 'variable';
}

// Helper to get an icon based on category (can be expanded)
const getCategoryIcon = (category: string) => {
  const catLower = category.toLowerCase();
  if (catLower.includes('rent') || catLower.includes('mortgage')) return <Home className="h-4 w-4" />;
  if (catLower.includes('groceries')) return <ShoppingCart className="h-4 w-4" />;
  if (catLower.includes('transportation') || catLower.includes('car')) return <Car className="h-4 w-4" />;
  if (catLower.includes('dining') || catLower.includes('restaurant')) return <Utensils className="h-4 w-4" />;
  if (catLower.includes('utilities')) return <Briefcase className="h-4 w-4" />;
  return <Activity className="h-4 w-4" />; // Default icon
};


export function ExpenseList({ title, expenses, type }: ExpenseListProps) {
  const filteredExpenses = expenses.filter(exp => exp.type === type);

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(amount);
  };

  return (
    <Card className="flex flex-col h-full shadow-lg rounded-xl">
      <CardHeader>
        <CardTitle className="text-primary">{title}</CardTitle>
        <CardDescription>Your {type} spending at a glance.</CardDescription>
      </CardHeader>
      <CardContent className="flex-grow overflow-hidden">
        {filteredExpenses.length === 0 ? (
          <div className="flex items-center justify-center h-full text-muted-foreground">
            <p>No {type} expenses recorded yet.</p>
          </div>
        ) : (
          <ScrollArea className="h-[300px] sm:h-[400px] pr-3">
            <ul className="space-y-3">
              {filteredExpenses.map(expense => (
                <li key={expense.id} className="flex items-center justify-between p-3 bg-background hover:bg-secondary/50 rounded-md border border-border transition-colors">
                  <div className="flex items-center space-x-3">
                    <span className="p-2 bg-primary/10 text-primary rounded-full">
                      {getCategoryIcon(expense.category)}
                    </span>
                    <div>
                      <p className="font-medium text-foreground">{expense.name}</p>
                      <p className="text-xs text-muted-foreground">
                        {format(expense.date, "MMM dd, yyyy")} - <Badge variant="outline" className="capitalize">{expense.category}</Badge>
                      </p>
                    </div>
                  </div>
                  <p className={`font-semibold ${type === 'essential' ? 'text-blue-600' : 'text-orange-600'}`}>
                    {formatCurrency(expense.amount)}
                  </p>
                </li>
              ))}
            </ul>
          </ScrollArea>
        )}
      </CardContent>
    </Card>
  );
}
